.contactLine{
    height: 40px;
    width: 90vw;
    margin-left: 5vw;
    margin-top: 5px;
    background-color: rgba(82, 87, 102, 0.322);
    border-radius: 3px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.flexDiv{
    border-right: 1px solid black;
    height: 30px;     
    padding: 0 10px 0 10px;
    box-sizing: border-box; 
    width: 25%;
}
.flexDiv select{
    width: 100%;
    height: 100%;
    background-color: rgba(240, 248, 255, 0);
    color: white;
}
.flexDiv option{
    background-color: rgb(48, 48, 92);
}
.contactLine:hover{
    background-color: rgba(82, 87, 102, 0.685);
}
.noMargin{
    margin: 0px;    
    position: absolute;
    bottom: 0;    
}
.hpx{
    width: 40px;    
}