html{
  height: 100%;
  width: 100%;
  background-image: linear-gradient(rgba(38, 0, 110, 0.486), rgba(95, 95, 214, 0), rgba(1, 1, 78, 0.404));  
  background-color: rgb(14, 20, 28);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}
.callBox{
  height: 300px;
  width: 90vw;
  background-color: rgba(82, 87, 102, 0.322);
  border-radius: 3px;
  position: absolute;
  top:20px;
  left: 5vw;
}
.callBoxSection{
  height: calc(100% - 70px);
  width: 33.33%;
  margin-top: 2%;  
  display: inline-block;  
  box-sizing: border-box;
  vertical-align: top;  
  position: relative;
}
.callBoxSection img{
  height: 90%;
  width: 90%;
  margin: 5%;
  object-fit: contain;  
}
.circleButton{
  height: 40px;
  width: 40px;
  border-radius: 50%;  
  display: inline-block;
  vertical-align: top;
  margin: 5px;
  position: relative;
  background-color: rgba(255, 255, 255, 0.404);
}
.greenHover:hover{
  background-color: rgb(8, 209, 8);
}
.redHover:hover{
  background-color: rgb(209, 8, 35);
}
.buttonRow{
  height: 50px;
  width: 100%;    
  display: flex;
  justify-content: space-between;
  align-items: center;

}
.section{
  height: 90%;
  width: 90%;
  background-color: rgba(82, 87, 102, 0.322);
  border-radius: 3px;
  margin-left: 5%;
  position: relative;
  color: white;
}
.contactsArea{
  position: absolute;
  top:360px;
  width: 100vw;
  left: 0;
  min-height:300px;
  height: calc(100vh - 380px);
  overflow-y: scroll;
  text-align: center;
}
.toolTip{
  display: none;
  visibility: hidden;
  position: absolute;    
  bottom: -30px;
  left: -80px;
  z-index: 2;
  background-color: rgba(255, 255, 255, 0.295);
  border-radius: 3px;
  padding: 0 5px 0 5px;
  min-height: 30px;
  min-width: 200px;
  line-height: 20px;
  display: flex;
  align-items: center;
  padding-top: 4px;
  box-sizing: border-box;
}
.toolTipParent{
  position: relative;
}
.toolTipParent:hover .toolTip{
  display: inline-block;
  visibility: visible;    
  background-color: rgba(82, 82, 133, 0.95);
}
.toolTipBigger{
  top: 40px;
  left: -50px;  
  width: 200px;
  min-height: 60px;
}
.toolTipBigger2{
  top: 40px;
  left: -50px;  
  width: 200px;
  min-height: 110px;
}
.circleButton:hover .toolTip{
  display: inline-block;
  visibility: visible;
  background-color: rgba(82, 82, 133, 0.95);
}
.bottomButtons{
  position: absolute;
  bottom: 0;
  width: 100%;
}
.bottomButton{
  height: 40px;
  overflow: hidden;
  box-sizing: border-box;
  border-left: 1px solid rgba(255, 255, 255, 0.541);
  background-image: linear-gradient(rgba(255, 255, 255, 0.466), rgba(0, 0, 255, 0), rgba(0, 0, 255, 0),rgba(0, 0, 0, 0.466));
  width: 33%;
  display: inline-block;
  user-select: none;
  color: white;
  font-weight: bold;
  line-height: 40px;
  position: relative;
}
.bottomButton input{
  visibility: hidden;
  position: absolute;
  left: 0;
  top:0;
  width: 100%;
  height: 100%;
}
.half{
  width: 50%;
}
.bottomButton:hover{
  background-color: rgba(255, 255, 255, 0.205);
}
.section textarea{
 width: 100%;
 height: calc(100% - 20px); 
 background-color: rgba(255, 222, 173, 0);
 padding: 10px;
 box-sizing: border-box;
 color: white;
 outline: none;
 border: none;
}
.flexSpace{
  display: flex;
  justify-content: space-around;
  padding-bottom: 10px;
}